import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-title',
  templateUrl: './title.component.html',
  styleUrls: ['./title.component.css']
})
export class TitleComponent implements OnInit {

  private typewriter_text: string = "woodRock";
  private typewriter_display: string = "";

  typingCallback(that) {
    let total_length = that.typewriter_text.length;
    let current_length = that.typewriter_display.length;
    if (current_length < total_length) {
      that.typewriter_display += that.typewriter_text[current_length];
      setTimeout(that.typingCallback, 500, that);
    } else {
      if (that.typewriter_display.slice(-1) == '|'){
        that.typewriter_display = that.typewriter_display.slice(0, -1);
      } else {
        that.typewriter_display += "|";
      }
      setTimeout(that.typingCallback, 500, that);
    }
  }

  constructor() { }

  ngOnInit() {
    this.typingCallback(this);
  }

}
