import {
    transition,
    trigger,
    query,
    style,
    animate,
    group,
    animateChild
 } from '@angular/animations';
 
 export const slideInAnimation =
    trigger('routeAnimations', [
         transition('Home => *', [
              query(':enter, :leave', 
                   style({ position: 'fixed',  width: '100%' }), 
                   { optional: false }),
              group([
                   query(':enter', [
                       style({ transform: 'translateX(100%)' }), 
                       animate('2s ease-in-out', 
                       style({ transform: 'translateX(0%)' }))
                   ], { optional: false }),
                   query(':leave', [
                       style({ transform: 'translateX(0%)' }),
                       animate('2s ease-in-out', 
                       style({ transform: 'translateX(-100%)' }))
                       ], { optional: false }),
               ])
         ]),
         transition('Home => About', [
               query(':enter, :leave', 
                   style({ position: 'fixed', width: '100%' }), 
                   { optional: false }),
               group([
                   query(':enter', [
                       style({ transform: 'translateX(100%)' }),
                       animate('2s ease-in-out', 
                       style({ transform: 'translateX(0%)' }))
                   ], { optional: false }),
                   query(':leave', [
                       style({ transform: 'translateX(0%)' }),
                       animate('2s ease-in-out', 
                       style({ transform: 'translateX(-100%)' }))
                   ], { optional: false }),
               ])
         ]),
         transition('About => Home', [
               query(':enter, :leave', 
                   style({ position: 'fixed', width: '100%' }), 
                   { optional: false }),
               group([
                   query(':enter', [
                       style({ transform: 'translateX(-100%)' }),
                       animate('2s ease-in-out', 
                       style({ transform: 'translateX(0%)' }))
                   ], { optional: false }),
                   query(':leave', [
                        style({ transform: 'translateX(0%)' }),
                        animate('2s ease-in-out', 
                        style({ transform: 'translateX(100%)' }))
                   ], { optional: false }),
               ])
        ]),
 ]);